.containerHome {
  display: flex;
  margin: 0 5%;
  padding: 60px 0 30px 0;
  justify-content: space-between;
}

.content {
  display: block;
  padding: 0 60px 0 0;
}

.topic {
  color: rgb(125, 121, 121);
  font-weight: 700;
  text-align: left;
}

.detailDescription {
  color: rgb(125, 121, 121);
  font-weight: 400;
  text-align: justify;
  padding-left: 3rem;
  font-size: clamp(14px, 4vw, 22px);
}

.profile {
  display: block;
  padding: 30px 0px 0 0;
  margin-right: 5%;
}
.imgProfile {
  border-radius: 200px 200px 200px 200px;
  width: 220px;
  height: 220px;
  box-shadow: 0 6px 10px 0 rgb(0 0 0 / 0%), 0 10px 30px 0 rgb(0 0 0 / 20%);
}


@media screen and (max-width: 745px) {
  .containerHome {
    display: flex;
    flex-direction: column-reverse;
  }

  .content{
    justify-content: center;
    padding: 0;
  }
  .detailDescription{
    padding-left: 1rem;
  }

  .topic{
    text-align: center;
  }
}
